export default {
	data() {
		return {
			money:'',
			authKey:'',
			part:'',
			popo:false
		};
	},
	onLoad(e) {
		this.authKey = uni.getStorageSync('authKey');
		this.join();
	},
	methods:{
		moneyInput: function(e) {
			this.money= e.target.value;
			console.log(this.money)
		},
		all(){
			this.money = this.part
		},
		// 获取金额
		join(){
			uni.request({
				url: this.http+'copemate/homepage',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
		                 this.part = res.data.data.income
					}
				},
			});
		},
		// 提现
		withdraw(){
			if(this.money==''){
				 uni.showToast({
					title: '请输入提现金额',
					icon:'none',
					mask: false,
					duration: 1500
				});
				return
			}
			if(this.money > this.part){
				uni.showToast({
					title: '提现余额不足',
					icon:'none',
					mask: false,
					duration: 1500
				});
				return
			}
			uni.request({
				url: this.http+'copemate/withdraw',
				method: 'POST',
				header: {
					authKey: this.authKey
				},
				data: {
					amount:this.money
				},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
			             uni.showToast({
			             	title: '提现成功',
			             	icon:'none',
			             	mask: false,
			             	duration: 1500
			             });
						 this.join();
						 this.popo = true
					}else{
						uni.showToast({
							title: res.data.error,
							icon:'none',
							mask: false,
							duration: 1500
						});
					}
				},
			});
		},
		close(){
			this.popo = false
		},
		godetail(){
			this.popo = false
		},
	}
}
